.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

main {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

header {
  display: flex;
  flex-direction: row;
  height: 10vmin;
  width: 100%;
  position: fixed;
  top: 0;
  padding: 1vmin;
  justify-content: space-between;
  vertical-align: center;
  font-size: calc(10px + 1vmin);
  background-color: darkslategray;
}

footer {
  display: flex;
  flex-direction: row;
  height: 2vmin;
  width: 100%;
  position: fixed;
  bottom: 0;
  padding: 1vmin;
  align-items: center;
  justify-content: center;
  vertical-align: center;
  font-size: calc(2px + 1vmin);
  background-color: darkslategray;
}

.body {
  display: flex;
  flex-direction: row;
  margin-top: 10vmin;
  margin-bottom: 2vmin;
  padding: 1vmin;
}

.banner {
  display: flex;
  flex-direction: row;
}

.student {
  display: flex;
  flex-direction: row;
  padding: 1vmin;
}

.status .details {
  display: flex;
  flex-direction: column;
}

.logo {
  height: 10vmin;
  pointer-events: none;
  align-self: left;
}
